import React, { useState, useEffect, useRef } from 'react';
import Layout from '../../components/Layout';
import {
  Box,
  Typography,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  TextField,
  ToggleButton,
  ToggleButtonGroup,
} from '@mui/material';
import VisibilityIcon from '@mui/icons-material/Visibility';
import { SnackbarAlert } from '../../components/placeholder/SnackbarAlert';
import { PromptSection } from '../../components/placeholder/PromptSection';
import { basicHTMLApp } from '../../helpers/git';
import LoginModal from '../../components/LoginModal';
import Parse from '../../vendor/Parse';
import Editor from '@monaco-editor/react';
import Frame from 'react-frame-component';

const AppPage = ({ location, placeholder }) => {
  const [state, setState] = useState({
    appName: 'My Web App',
    prompt: '',
    enhancedPrompt: '',
    files: {
      'index.html': basicHTMLApp,
      'style.css': '',
      'script.js': '',
    },
    snackbarOpen: false,
    snackbarMessage: '',
    snackbarSeverity: 'info',
    systemPrompt: 'create web application in a single html file, making sure UI is properly aligned and user experience is taken care of',
    showCodeEditor: true,
    showAppOutput: true,
    showAI: true,
    loginModalOpen: false,
    user: null,
    isAppDeployed: false,
    deployModalOpen: false,
    isDeploying: false,
    deployStatusMessage: '',
    isPromoting: false,
    promoteModalOpen: false,
    src: placeholder,
    dest: '',
    token: '',
    previewMode: 'live',
  });

  const frameRef = useRef(null);
  const [frameLoaded, setFrameLoaded] = useState(false);
  const [previewUrl, setPreviewUrl] = useState(`https://build.appseek.ai/${placeholder}`);
  const [previewUrlInput, setPreviewUrlInput] = useState(`https://build.appseek.ai/${placeholder}`);

  const {
    appName,
    prompt,
    enhancedPrompt,
    files,
    snackbarOpen,
    snackbarMessage,
    snackbarSeverity,
    systemPrompt,
    loginModalOpen,
    user,
    isAppDeployed,
    deployModalOpen,
    isDeploying,
    deployStatusMessage,
    src,
    token,
    previewMode,
  } = state;

  const queryParams = new URLSearchParams(location.search);
  const showPromoteButton = queryParams.has('p') && isAppDeployed;
  const urlPrompt = queryParams.get('prompt');

  const fetchAppData = async () => {
    try {
      const result = await Parse.Cloud.run('getApp', { id: placeholder });
      if (result?.files && Object.keys(result.files).length > 0) {
        const normalizedFiles = {
          'index.html': result.files['index.html'] || basicHTMLApp,
          'style.css': result.files['style.css'] || '',
          'script.js': result.files['script.js'] || '',
        };

        setState((prev) => ({
          ...prev,
          files: normalizedFiles,
          isAppDeployed: true,
          appName: result?.appName || 'My Web App',
        }));
      } else {
        setState((prev) => ({
          ...prev,
          isAppDeployed: false,
          files: {
            'index.html': basicHTMLApp,
            'style.css': '',
            'script.js': '',
          },
        }));
      }
    } catch (error) {
      console.error('Error fetching app data:', error);
      setState((prev) => ({
        ...prev,
        isAppDeployed: false,
        files: {
          'index.html': basicHTMLApp,
          'style.css': '',
          'script.js': '',
        },
        snackbarMessage: 'Failed to load app data',
        snackbarSeverity: 'error',
        snackbarOpen: true,
      }));
    }
  };

  useEffect(() => {
    fetchAppData();
  }, [placeholder]);

  useEffect(() => {
    const combinedCode = Object.values(files).join('\n\n');
    const enhancedPrompt = `${combinedCode}\n\n${prompt}\n\n${systemPrompt}`;
    setState((prev) => ({ ...prev, enhancedPrompt }));
  }, [files, prompt, systemPrompt]);

  useEffect(() => {
    const currentUser = Parse.User.current();
    if (currentUser) setState((prev) => ({ ...prev, user: currentUser }));
  }, []);

  useEffect(() => {
    if (urlPrompt) {
      setState((prev) => ({ ...prev, systemPrompt: urlPrompt }));
    }
  }, [urlPrompt]);

  const handleCopyPrompt = () => {
    navigator.clipboard.writeText(enhancedPrompt).then(() => {
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'Prompt copied to clipboard!',
        snackbarSeverity: 'success',
        snackbarOpen: true,
      }));
    }).catch(() => {
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'Failed to copy prompt',
        snackbarSeverity: 'error',
        snackbarOpen: true,
      }));
    });
  };

  const handleDeploy = async () => {
    try {
      await Parse.Cloud.run('deployApp', { files, placeholder, appname: appName.trim() });
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'App deployed successfully!',
        snackbarSeverity: 'success',
        snackbarOpen: true,
        isAppDeployed: true,
        deployStatusMessage: 'A link will appear beside the app name',
        isDeploying: false,
      }));
      setTimeout(() => setState((prev) => ({ ...prev, deployStatusMessage: '' })), 3000);
    } catch {
      setState((prev) => ({
        ...prev,
        snackbarMessage: 'Failed to deploy app. Please try again.',
        snackbarSeverity: 'error',
        snackbarOpen: true,
        isDeploying: false,
      }));
    }
  };

  const handleFilesChange = (filePath, newCode) => {
    setState((prev) => ({
      ...prev,
      files: { ...prev.files, [filePath]: newCode },
    }));
  };

  const handleFrameLoad = () => setFrameLoaded(true);

  const generateIframeContent = () => {
    const { 'index.html': htmlCode = '', 'style.css': cssCode = '', 'script.js': jsCode = '' } = files;
    return `<!DOCTYPE html><html><head><meta charset="UTF-8"><meta name="viewport" content="width=device-width, initial-scale=1.0"><style>${cssCode}</style></head><body>${htmlCode}<script>${jsCode}</script></body></html>`;
  };

  return (
    <Layout deployButton={<Button variant="contained" onClick={() => setState(prev => ({ ...prev, deployModalOpen: true }))}>Deploy</Button>}>
      <Typography variant="h5" sx={{ mt: 2, mb: 3, fontWeight: 600 }}>{appName}</Typography>

      <ToggleButtonGroup
        value={previewMode}
        exclusive
        onChange={(e, val) => val && setState((prev) => ({ ...prev, previewMode: val }))}
        sx={{ mb: 2 }}
      >
        <ToggleButton value="live">Live Preview</ToggleButton>
        <ToggleButton value="external">Deployed Preview</ToggleButton>
      </ToggleButtonGroup>

      <Box sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, gap: 2, mb: 4, minHeight: '600px', height: 'calc(100vh - 250px)', overflow: 'hidden' }}>
        <Box sx={{ flex: 1, minHeight: 0, display: 'flex', flexDirection: 'column', border: '1px solid #ccc' }}>
          <Editor
            height="100%"
            width="100%"
            theme="vs-light"
            defaultLanguage="html"
            value={files['index.html']}
            onChange={(value) => handleFilesChange('index.html', value)}
            options={{ minimap: { enabled: false }, fontSize: 14, scrollBeyondLastLine: false }}
          />
        </Box>
        <Box sx={{ flex: 1, minHeight: 0, display: 'flex', flexDirection: 'column', border: '1px solid #ccc' }}>
          {previewMode === 'live' ? (
            <Frame
              ref={frameRef}
              style={{ width: '100%', height: '100%', border: 'none' }}
              initialContent={generateIframeContent()}
              onLoad={handleFrameLoad}
              mountTarget="#mount-target"
            >
              {frameLoaded && <div id="mount-target" />}
            </Frame>
          ) : (
            <Box sx={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
              <Box sx={{ display: 'flex', gap: 1, alignItems: 'center', p: 1, backgroundColor: '#f5f5f5', borderBottom: '1px solid #ccc' }}>
                <TextField
                  fullWidth
                  variant="outlined"
                  size="small"
                  value={previewUrlInput}
                  onChange={(e) => setPreviewUrlInput(e.target.value)}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      setPreviewUrl(previewUrlInput);
                    }
                  }}
                />
                <Button variant="contained" onClick={() => setPreviewUrl(previewUrlInput)}>Go</Button>
              </Box>
              <iframe
                title="Deployed App Preview"
                src={previewUrl}
                style={{ flex: 1, width: '100%', border: 'none' }}
              />
            </Box>
          )}
        </Box>
      </Box>

      <Box sx={{ flex: 2, width: '100%' }}>
        <PromptSection
          prompt={prompt}
          setPrompt={(value) => setState((prev) => ({ ...prev, prompt: value }))}
          enhancedPrompt={enhancedPrompt}
          handleCopyPrompt={handleCopyPrompt}
          systemPrompt={systemPrompt}
          setSystemPrompt={(value) => setState((prev) => ({ ...prev, systemPrompt: value }))}
        />
      </Box>

      <LoginModal
        open={loginModalOpen}
        onClose={() => setState((prev) => ({ ...prev, loginModalOpen: false }))}
        onLoginSuccess={() => {
          const currentUser = Parse.User.current();
          setState((prev) => ({ ...prev, user: currentUser, loginModalOpen: false }));
        }}
      />

      <Dialog open={deployModalOpen} onClose={() => setState((prev) => ({ ...prev, deployModalOpen: false }))}>
        <DialogTitle>Bookmark This App</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Before deploying, we recommend bookmarking this app to avoid losing it.
          </DialogContentText>
          <TextField
            autoFocus
            fullWidth
            label="App Name"
            value={state.appName}
            onChange={(e) => setState((prev) => ({ ...prev, appName: e.target.value }))}
            sx={{ mt: 1 }}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setState((prev) => ({ ...prev, deployModalOpen: false }))}>Cancel</Button>
          <Button onClick={() => { setState((prev) => ({ ...prev, isDeploying: true, deployModalOpen: false })); handleDeploy(); }} color="primary" variant="contained" disabled={isDeploying}>
            {isDeploying ? 'Deploying...' : 'Deploy'}
          </Button>
        </DialogActions>
      </Dialog>

      <SnackbarAlert
        open={snackbarOpen}
        message={snackbarMessage}
        severity={snackbarSeverity}
        onClose={() => setState((prev) => ({ ...prev, snackbarOpen: false }))}
      />
    </Layout>
  );
};

export default AppPage;
